  import topNav from '../../components/navigation.vue'
	export default {
        components:{
    			topNav
    		},
	    data() {
	        return {
	            indicatorDots: true,
	            autoplay: true,
	            interval: 2000,
	            duration: 500,
             // 自定义导航栏对象
               setNav:{
              'bg':'#ffffff',  //背景色
              'color':'red',  //字体颜色
              'isdisPlayNavTitle':false, //是否显示返回按钮，由于导航栏是共用的，把所有的东西封装好，
              // 然后有些页面不需要的东西通过条件控制进行显示与隐藏
              'navTitle':'懂车帝', //导航标题
              'imgUrl':'../static/logo.png'
              },
              // 导航
              navtext:[{
                   name:'全部车型',
                   imgUrl:'/static/img/carType.png'
              },
              {
                   name:'拍照识车',
                   imgUrl:'/static/img/camera.png'
              },
              {
                   name:'新车发布',
                   imgUrl:'/static/img/newCar.png'
              },
              {
                   name:'销量排行',
                   imgUrl:'/static/img/trophy.png'
              }],
	        }
	    },
	    methods: {
	        changeIndicatorDots(e) {
	            this.indicatorDots = !this.indicatorDots
	        },
	        changeAutoplay(e) {
	            this.autoplay = !this.autoplay
	        },
	        intervalChange(e) {
	            this.interval = e.target.value
	        },
	        durationChange(e) {
	            this.duration = e.target.value
	        }
	    }
	}
